import React, {useContext, useState} from 'react';
import './Signup.css';
import {useHistory} from 'react-router-dom';
import {formValidator} from '../../formValidator';
import {UserContext} from '../../UserContext';


function Signup(props) {
    
    const [fname, setFname] = useState('');
    const [sname, setSname] = useState('');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [error, setError] = useState(null);
    const history = useHistory();
    const {userDetails, setUserDetails} = useContext(UserContext)

    const formSubmitHandler = (event) => {
        let result = formValidator(fname, sname, email, password);
        //console.log(result); // this will show the result
        if (result.status) {
          setError(result);
          //console.log(error);
          alert(error)
        }
        else {
          setError(null);
          //console.log(result.status);
          const encodedPassword = btoa(password)
          console.log(encodedPassword)
          setUserDetails([...userDetails, {id: Date.now(), firstname: fname, secondname: sname, emailid: email, password: encodedPassword}])
          console.log(userDetails);
          history.push('/login')
        }
      };
    return (
        <div className="body">
            <div className="wrapper fadeInDown">
            <div id="formContent">
                
                <h2 class="active">Sign Up</h2>
               
                <form>
                <input type="text" onChange={(event) => setFname(event.target.value)} className="fadeIn first" htmlFor="fname" placeholder="first name"/>
                <input type="text" onChange={(event) => setSname(event.target.value)} className="fadeIn second" htmlFor="sname" placeholder="second name"/>
                <input type="email" onChange={(event) => setEmail(event.target.value)} htmlFor="email" className="fadeIn third" placeholder="email"/>
                <input type="password" onChange={(event) => setPassword(event.target.value)} htmlFor="password" className="fadeIn fourth" placeholder="password"/>
                <input type="submit" onClick={(event)=>{formSubmitHandler();
                event.preventDefault();}} class="fadeIn fourth" value="Sign Up"/>
                </form>

            </div>
            </div>
        </div>
    )
}

export default Signup
