import React,{useState, useContext, useEffect} from 'react';
import './Login.css';
import {useHistory} from 'react-router-dom';
import {formValidator} from '../../formValidator';
import {UserContext} from '../../UserContext'



function Login() {
    const {userDetails, setUserDetails, userLog, setUserLog} = useContext(UserContext);
    useEffect(() => {
        console.log(userDetails);
    }, [])
    const [usname, setUsname] = useState('');
    const [pass, setPass] = useState('');
    const [errors, setErrors] = useState(null);
    const history = useHistory();

    const formHandle = ()=>{
        let result = formValidator(usname, pass);
        if (result.status) {
          setErrors(result);
          alert(errors)
          
        } else {
          setErrors(null);
          const passwords = btoa(pass);
          const found = userDetails.find(userDetails => userDetails.firstname === usname && userDetails.password === passwords)
          if(found)
          {
              alert("You are sucessfully logging in!")
              history.push('/')
                // Store
                localStorage.setItem("loggedInUser", JSON.stringify(found));
                setUserLog({value: true})
          }else{
              alert("Your credentails are incorrect.")
          }
        }
    }
    return (
        <div className="body">
            <div className="wrapper fadeInDown">
            <div id="formContent">

                <h2 className="active"> Sign In </h2>
                <form>
                <input onChange={(event) => setUsname(event.target.value)} type="text" className="fadeIn second" htmlFor="usname" placeholder="login"/>
                <input onChange={(event) => setPass(event.target.value)} type="password" htmlFor="pass" className="fadeIn third" placeholder="password"/>
                <input  type="submit" onClick={formHandle} class="fadeIn fourth" value="Log In"/>
                </form>

            </div>
            </div>
        </div>
    )
}

export default Login
