import axios from 'axios'
import {imageUrl, API_KEY} from '../../Constants/conStants.js'
import React, { useEffect, useState } from 'react'
import YouTube from '../Trailer/YouTube'
import './Rowpost.css'
function RowPost(props) {
    const [trends, setTrends] = useState([])
    const [trail, setTrail] = useState('')
    useEffect(()=>{
        let urls = props.url
        axios.get(urls).then((response)=>{
            setTrends(response.data.results)
        })
    })
    const movieTrailer = (id)=>{
        console.log(id)
        axios.get(`https://api.themoviedb.org/3//movie/${id}/videos?api_key=${API_KEY}&language=en-US`).then((response)=>{
            if(response.data.results.length!==0){
                setTrail(response.data.results[0])
            }else{
                console.log("Invalid");
            }
        })

    }
    return (
        <div className="row">
            <h2>{props.title}</h2>
            <div className='rowposts'>
                {trends.map((obj)=>
                
                    <img onClick={()=>{
                        console.log(obj.backdrop_path)
                        movieTrailer(obj.id)
                    }}
                    className={props.isSmall ? 'smallposts': 'posts'} 
                    src={`${imageUrl+obj.backdrop_path}`} alt="Poster" />
                )}
            

                </div>
                <YouTube trail={trail}/>
        </div>
    )}


export default RowPost
