
export const formValidator = (fname, sname, email, password, usname, pass) => {
    //console.log('datas should be validate :', fname, sname, email, password); // this will show first name last name
    //console.log(typeof lname);
    let errMsg = {
      status: false,
    };
    if (fname === '') {
      errMsg.fname = '*This field is required';
      errMsg.status = true;
      alert(errMsg.fname)
    }
    else if(sname === '') {
      errMsg.sname = '*This field is required';
      errMsg.status = true;
      alert(errMsg.sname)
    }
    else if(email === '') {
        errMsg.email = '*This field is required';
        errMsg.status = true;
        alert(errMsg.email)
    }
    else if (password === '') {
        errMsg.sname = '*This field is required';
        errMsg.status = true;
        alert(errMsg.password)
      }
    else if (usname === '') {
        errMsg.usname = '*This field is required';
        errMsg.status = true;
        alert(errMsg.usname)
      }
    else if (pass === '') {
        errMsg.pass = '*This field is required';
        errMsg.status = true;
        alert(errMsg.pass)
      }
    return errMsg;
  };