import React, {useContext} from 'react'
import {API_KEY, imageUrl} from '../../Constants/conStants.js'
import './Banner.css'
import axios from'../../axios'
import {useEffect,useState} from 'react'
import {useHistory} from 'react-router-dom'
import {UserContext} from '../../UserContext'

function Banner() {
    var arr = JSON.parse( localStorage.getItem('loggedInUser') );
    console.log(arr)
    const {userDetails, setUserDetails, userLog, setUserLog} = useContext(UserContext)
    const history = useHistory()
    const [movie, setMovie] = useState([])
    useEffect(()=>{
        /*console.log(userDetails)*/
        /*console.log(userLog.value)*/
        axios.get(`trending/all/week?api_key=${API_KEY}&language=en-US`).then((response)=>{
            let x =  Math.floor(Math.random() * 20)
            setMovie(response.data.results[x])
        })
    },[])
    return (
       <div>
        
        <div style={{backgroundImage: 
        `url(${movie ? imageUrl+movie.backdrop_path: ""})`}} 
        className="banner">
        <div className="content">
            <h1 className="title">{movie.title}</h1>
                <div className="banner_button">
                    <h6>Login to see the trailer videos.</h6>
                    {arr == null && <button onClick={()=>history.push('/login')} className="button">
                        Login
                    </button>}
                    {arr == null &&<button onClick={()=>history.push('/signup')} className="button">
                        Signup
                    </button>}
                    {arr != null && <button onClick={()=>{
                        localStorage.removeItem("loggedInUser")
                        setUserLog({value:false})}}
                        className="button">
                        Logout
                    </button>}
                </div>
                <h1 className="description">
                {movie.overview}
                </h1>
            </div>
            <div className="fade_bottom"></div>
        </div>
       </div>
    )}
export default Banner